$segment-description-color: $description-color;
$header-height: 56px;
$inline-filter-height: 52px;
$tab-height: $inline-filter-height;
$filter-breakpoint: 1024px;

.float-right {
	float: right; }

.float-left {
	float: left; }

.text-center {
	text-align: center; }

.main.container {
	margin-top: 7em; }

.text.aligned.right {
	text-align: right; }

.ui.segment .description {
	color: $segment-description-color; }

.ui.hide {
	display: none; }

.ui.dim {
	color: $segment-description-color; }

.header.expanded {
	position: fixed;
	width: 100%;
	background-color: #ffffff;
	z-index: 99;
	height: $header-height; }

.sep {
	border-bottom: 1px solid #eee; }

.ui.secondary.menu.top {
	margin-bottom: 0;
	margin-top: 0; }

.ui.secondary.menu.submenu {
	margin-left: 2rem;
	width: 12rem;
	display: none; }

.ui.secondary.menu .item.active + .submenu {
	display: block; }

.offcanvas-sidebar, .main-sidebar {
	.ui.vertical.menu .item > i.icon {
		float: none;
		margin-right: 8px; } }

.filter-holder {
	position: fixed;
	top: $header-height;
	width: 100%;
	z-index: 90;
	background-color: $white;
	box-shadow: 0 0 8px #ccc; }

.filter-tabs {
	display: none;
	> .tabs {
		height: $tab-height; }

	@media screen and (max-width: $filter-breakpoint ) {
		display: block; } }

.filter-search {
	height: $inline-filter-height;
	@media screen and (max-width: $filter-breakpoint ) {
		display: none; } }


.ui.basic.filter.buttons .active.button {
	background-color: $theme-color !important;
	color: $white !important; }
