.box {
	padding: 1em;
	border: 1px solid rgba(34, 34, 34, 0.1);
	border-radius: 4px; }

%primary-box {
	background-color: #E7F3FB;
	border-color: #A8B3BB; }

.ui.cards .card.primary-box {
	@extend %primary-box;
	padding: 1.5em; }

.img-container {
	width: 100%;
	overflow: hidden;
	img {
		width: 100%; } }

.ui.items > .item > .content > .header:not(.ui) {
	font-weight: 400; }

.img-header {
	margin-top: 0.5em;
	margin-bottom: 0.5em; }

.ui.button.request {
	text-transform: uppercase;
	background-color: #BCC6D0;
	color: white;
	font-weight: 100; }

.padder {
	padding: 1em; }

// .ui.card .ui.items > .item
// 	padding: 1em
.item .padder {
	padding-top: 0;
	padding-bottom: 0; }

.item:first-child .padder {
	padding-top: 1em; }

.item:last-child .padder {
	padding-bottom: 1em; }

.ui.divided.items.blog > {
	.item {
		padding: 1.2rem 0;
		.header {
			font-size: 1.128rem; } } }

.ui.table thead th {
	color: #9b9b9b;
	font-weight: 400;
	font-size: 10px;
	text-transform: uppercase; }

// .ui.table
// 	.header
// 		color: #555

.dim {
	color: #9b9b9b; }

.status {
	text-transform: uppercase;
	font-size: 0.8rem;
	font-weight: 400; }

.status.pending {
	color: #E0A200; }

.status.disapproved {
	color: #FE0000; }

.status.eligible {
	color: #18C61B; }

.mr-1 {
	margin-right: 1rem; }

.header_affiliate {
	text-transform: uppercase;
	font-size: 12px;
	color: #2987CD;
	padding-left: 1rem; }

.desc_black {
	color: #000000;
	padding-left: 2px; }

@media only screen and (min-width: 767px) {
	.ui.dashboard > .inline.fields {
		justify-content: flex-end; } }

@media only screen and (max-width: 767px) {
	.ui.stackable.grid .ui.form .inline.fields .field {
		margin-bottom: 1rem; }

	.mobile.hidden {
		display: none; } }


@media only screen and (max-width: 767px) {
  [class*="mobile hidden"],
  [class*="tablet only"]:not(.mobile),
  [class*="computer only"]:not(.mobile),
  [class*="large monitor only"]:not(.mobile),
  [class*="widescreen monitor only"]:not(.mobile),
  [class*="or lower hidden"] {
    display: none !important; } }
