.ui.primary.text {
	color: #2185D0; }

.ui.blue.text {
	color: #2185D0; }

.ui.purple.text {
	color: #CD29AC; }

@media only screen and (min-width: 768px) {
	.pl-tablet-0 {
		padding-left: 0 !important; } }

.footer {
	margin-top: 2rem; }

.hide {
	display: none !important; }

.banner {
	width: 100%;
	background-size: cover;
	background-repeat: no-repeat; }


.ui.menu:not(.secondary):not(.text):not(.tabular):not(.borderless) > .container > .item:not(.right):not(.borderless):first-child {
	border-left: 0; }

/**
 * Segment
 */
.ui {
	&.segment {
		.secondary.description {}
		color: $secondary-description-color; }
	&.form {
		.field > label {
			color: $label-color; } } }



/**
 * Form fields
 */
.ui.form .field, .ui.form .fields {
	margin-bottom: 1.5rem; }

.ui.form .fields .fields {
	margin-bottom: 0; }

h2.ui.form-title {
	font-size: 1.2rem; }



.ui.dividing.header {
	font-weight: normal;
	border: 0;
	border-top: 1px solid rgba(34, 36, 38, 0.15);
	padding-top: 1rem;
	margin-top: 2.5rem;

	&:first-child {
		margin-top: 0.5rem; } }


/**
 * Steps
 */
.ui.steps {
	.step {
		// Pull content to the left
		-webkit-justify-content: initial;
		-ms-flex-pack: initial;
		justify-content: initial;
		padding-top: 0.8em;
		padding-bottom: 0.8em;
		.icon {
			color: #BFBFBF; }
		&.active .icon {
			color: #4183C4; }

		.description {
			color: $description-color; } } }

/**
 * Vertical Menu
 */
.account-sidebar .ui.secondary.menu .active.item {
	background-color: transparent;
	font-weight: bold;
	color: inherit; }

.account-sidebar .ui.secondary.menu .item {
	color: #9B9B9B;

	&:hover {
		background-color: transparent; } }


/**
 * Custom Component
 * Sidebar
 */
.account-sidebar {
	width: 340px;
	left: 0;
	top: 0;
	height: 100%;
	position: fixed;
	padding-top: 96px;
	padding-left: 64px;
	padding-bottom: 32px;
	padding-right: 52px;
	background-color: #F7F7F7;
	overflow-y: auto; }

.content-with-sidebar {
	margin-left: 380px;
	margin-right: 40px; }


// .main-navigation-side



/**
 * Tablet
 */
@media only screen and (max-width: 991px) {
	.account-sidebar {
		padding-left: 16px;
		padding-top: 76px;
		padding-right: 16px;
		width: 260px; }

	.content-with-sidebar {
		margin-left: 290px;
		margin-right: 30px; } }

@media only screen and (min-width: 768px) {
	.sidebar-menu-handler {
		display: none !important; } }

@media only screen and (max-width: 767px) {
	.sidebar-item {
		display: none !important; } }


/**
 * Breakpoint to display sidebar
 * Below the specified device width the side bar disappears and a hamburger menu
 * is visible at the top to toggle sidebar
 */
@media only screen and (max-width: 767px) {
	.main-sidebar {
		display: none; }

	.content-with-sidebar {
		margin-left: 0;
		margin-right: 0; } }


.theme-color {
	color: $theme-color; }




/**
 * Card on the signup form that is displays the information about the user
 */

.ui.form-header {
	display: none;
	margin-top: 1rem; }

/**
 * Mobile media queries
 */
@media only screen and (min-width: 320px) and (max-width: 767px) {
	.ui.form-step {
		display: none; }

	.ui.form-header {
		display: block; }

	.order-first-mobile {
		order: -1; } }

@media only screen and (min-width: 768px) {
	.ui.side-card {
		padding: 2rem;
		text-align: center; } }

/**
 * Accordion
 */
.ui.accordion.request-form {
	width: auto;
	margin-bottom: 12px;

	.accordion-wrapper {
		margin-bottom: 1rem;
		border: 1px solid rgba(34, 36, 38, 0.15);
		padding:  0 0.8rem;
		border-radius: 4px; }

	.title {
		background-color: #F8F8F8;
		border-radius: 3px;
		border-top: 0;
		margin-left: -0.8rem;
		margin-right: -0.8rem;
		padding: 0.8rem;

		> .checkbox {
			font-weight: bold; }

		&.active {
			border-bottom: 1px solid rgba(34, 36, 38, 0.15);
			border-bottom-left-radius: 0;
			border-bottom-right-radius: 0;
			margin-bottom: 0.8rem; }

		.dropdown.icon {
			float: right; } } }

