$top-bar-height: $inline-filter-height + $header-height;


.content-holder {
	padding-top: $top-bar-height;
	height: 100vh; }

.left-column, .right-column, .content-holder {
	display: flex; }

.left-column {
	position: relative; }



.right-column {
	flex-grow: 1;
	position: relative; }


.content-scroll {
	overflow-y: auto;
	height: 100%; }

.map-overlay {
	transition: background 300ms; }

.doctors-list {
	display: flex;
	background-color: #FFF;
	position: relative;
	z-index: 3;
	box-shadow: 8px 0 16px -5px rgba(0,0,0,0.1);

	.ui.list > .item {
		padding: 16px 8px 16px 24px;
		cursor: pointer;

		&.active {
			background-color: #F3F4F5; } }

	&-detail {
		position: absolute;
		height: 100%;
		z-index: 2;
		background-color: #FFF;
		transform: translateX(0);
		transition: transform 300ms;
		overflow: auto;
		right: 0;

		.close {
			padding: 8px;
			margin-bottom: -16px; }

		&.active {
			box-shadow: 8px 0 16px -5px rgba(0,0,0,0.1);
			transform: translateX(100%);

			& + .map-overlay {
				position: absolute;
				height: 100%;
				width: 100%;
				background-color: rgba(255,255,255,0.5);
				z-index: 1; } }

		.ui.list > .item {
			padding: 16px 16px 16px 24px; } }

	&-search {
		display: flex;
		flex-basis: auto;
		flex-grow: 0;
		flex-shrink: 0; }


	.item {
		position: relative; } }

.map-holder, .map {
	height: 100%;
	width: 100%; }


.map-holder {
	@media screen and (max-width: $filter-breakpoint ) {
		height: calc(100vh - #{$top-bar-height} ); } }

.doctor-affiliated i {
	color: #F5A623; }

.doctor-affiliated .content .header span {
	color: #2987CD; }

.doctor-affiliated .content .header span, .doctor-nonaffiliated .content .header span {
	text-transform: uppercase;
	font-size: 12px;
	padding-left: 1rem; }

.doctor-nonaffiliated i, .doctor-nonaffiliated .content .header span {
	color: #9B9B9B; }

.doctors-list .description > p {
	margin-bottom: 0;
	color: rgba(0, 0, 0, 0.4); }

.doctors-list .description > .list-footer {
	margin-top: 8px; }

.doctor-affiliated .description p span, .doctor-nonaffiliated .description p span {
	padding-left: 2px;
	color: #000000; }

.mb_zero {
	margin-bottom: 0; }
