.ui.card-table {
	border: 0;
	font-size: 0.875rem;
	height: 100%;
	border-radius: 0;
	border-collapse: collapse;

	thead tr:first-child > th:only-child {
		border-radius: 0; }
	.checkbox {
		font-size: 0.875rem; }

	thead th {
		background-color: #F3A537;
		color: #FFF;
		font-size: 16px;
		text-transform: none;
		border: 0; }

	td {
		border-top: 0;
		padding-top: 4px;
		padding-bottom: 4px; } }

.cards-holder {
	.column {
		padding: 0 !important; }

	.inner-container {
		max-height: 200px;
		background-image: url('../img/site-logo.png');
		background-size: auto 12px;
		background-repeat: no-repeat;
		background-position: bottom 24px right 24px;

		> td {
			padding-top: 24px; } }

	td {
		vertical-align: top; } }
