.six.wide.column.pr_zero {
	padding-right: 0; }

.ui.menu > .item.label {
	padding-right: 0; }


.filter-search {
	border-top: 1px solid #eee;

	.ui.menu {
		box-shadow: none;

		&:not(.vertical) .item > .button {
			font-size: 0.875rem; } }

	.item.label {
		font-size: 0.875rem; }

	.ui.basic.buttons .active.button {
		background-color: $theme-color !important;
		color: $white !important; } }

.hospital.icon, .caret.right.icon {
	color: $theme-color; }

.get_direction, .get_direction .icon, {
	font-size: 10px;
	margin-left: 5px;
	color: $theme-color; }

@media screen and (max-width: 768px) {
	.doctors-list .ui.list > .item {
		padding-left: 16px; } }

.filter-button {
	position: fixed;
	left: 50%;
	transform: translatex( -50%);
	bottom: 8px;

	> .ui.button {
		background-color: $white;
		border-radius: 100px;
		box-shadow: 2px 2px 8px rgba(0,0,0,0.2); } }


@media screen and (min-width: $filter-breakpoint + 1px) {
	.show-for-tab {
		display: none; } }

@media screen and (max-width: $filter-breakpoint ) {
	.show-for-tab {
		display: block; }

	.content-holder .tab-target {
		display: none; }

	.content-holder .tab-target.active {
		display: flex; }

	.doctors-list-search {
		flex-grow: 1;
		flex-shrink: 1;

		> .list > .item:last-child {
			margin-bottom: 32px; } }

	.doctors-list-detail {
		z-index: 5;
		transform: translate(100%);

		> .list > .item:last-child {
			margin-bottom: 32px; }

		&.active {
			transform: translate(0);
			box-shadow: 8px 0px 16px 11px rgba(0, 0, 0, 0.1); } }

	.left-column.tab-target {
		width: 100%;
		position: relative;

		.doctors-list {
			width: 100%;

			.list {
				width: 100%; } } } }

